.App {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.toolbox{
  height:100vh;
  background-color: #3a4b4b;
}

.toolbox .group{
  background-color:#3a4b4b;
  border-bottom: thin solid gray;
}

.toolbox .group .item{
  width:24px;
  height: 24px;
  padding:10px;
  cursor: pointer;
}


.toolbox .group .item.active, .toolbox .group .item:hover{
  background-color:#4b6161;
}

.toolbox .group.stroks .item{
  position: relative;
}

.group.stroks .item .stroke{
  background-color: white;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  top:50%;
  left:50%;
}

.group.colors .item .swatch{
  box-sizing: border-box;
  width:100%;
  height: 100%;
}

.canvas-box{
  background-color:white;
  height: 500px;
  width: 500px;
}

.stroke-1 {
  width: 2px;
  height: 2px;
}
.stroke-2 {
  width: 6px;
  height: 6px;
}
.stroke-3 {
  width: 10px;
  height: 10px;
}
.stroke-4 {
  width: 14px;
  height: 14px;  
}
.stroke-5 {
  width: 18px;
  height: 18px;  
}